import React, { Component } from 'react'
import NewsItem from './NewsItem'
import PropTypes from 'prop-types'
import Spinner from './Spinner'
import InfiniteScroll from "react-infinite-scroll-component"
export class News extends Component {

    articles = []
    static defaultProps = {
        country: 'in',
        category: 'general'
    }
    static propsTypes = {
        country: PropTypes.string,
        pageSize: PropTypes.number
    }

    constructor(props) {
        super(props);
        console.log('Hello Bhai ')
        this.state = {
            articles: [],
            loading: false,
            totalResults:0,
        
        }
        document.title = `${this.props.category} -CHIKU News`
    }
    async updateNews(pageNo) {
        console.log('cdn')
        const url = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=ad4176ee46a84c4b9912bdd3c41714b8&page==${this.state.page}&pageSize=15`;

        let data = await fetch(url)
        let parsedData = await data.json()
        console.log(parsedData)
        this.setState({ articles: parsedData.articles, totalResults: parsedData.totalResults })
    }
     componentDidMount() {
        //  console.log('cdn')
        //     let url=`https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=ad4176ee46a84c4b9912bdd3c41714b8&page=1&pageSize=20`;

        //     let data= await fetch(url)
        //     let parsedData= await data.json()
        //     console.log(parsedData)
        //     this.setState({articles:parsedData.articles,totalResults:parsedData.totalResults})
        this.updateNews();

    }


    handlenextClick = async () => {
        // if( this.state.page+1>Math.ceil(this.state.totalResults/20)){

        // }
        // {
        //     let url=`https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=ad4176ee46a84c4b9912bdd3c41714b8&page=${this.state.page+1}1&pageSize=20`

        //     let data= await fetch(url)
        //     let parsedData= await data.json()
        //     console.log(parsedData)
        //   this.setState({

        //       page:this.state.page+1,
        //       articles:parsedData.articles

        //   })
        // }
        this.updateNews();

        this.setState({ page: this.state.page + 1 })

    }
    handleprevClick = async () => {
        //     let url= `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=ad4176ee46a84c4b9912bdd3c41714b8&page=${this.state.page -1}&pageSize=20`

        //     let data= await fetch(url)
        //     let parsedData= await data.json()
        //     console.log(parsedData)
        //   this.setState({

        //       page:this.state.page-1,
        //       articles:parsedData.articles

        //   })
        this.setState({ page: this.state.page - 1 })
        this.updateNews()

    }
    fetchMoreData = async() => {
        // a fake async api call like which sends
       this.setState({
           page:this.state.page +1
       })
       
        console.log('cdn')
        const url = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=ad4176ee46a84c4b9912bdd3c41714b8&page==${this.state.page}&pageSize=20`;

        let data = await fetch(url)
        let parsedData = await data.json()
        console.log(parsedData)
        this.setState({ articles: this.state.articles.concat(parsedData.articles), totalResults: parsedData.totalResults })
    
      };







    render() {
        console.log('render')
        return (
<>


           <div className="container my-3" >
                <h2 >Top HeadLines Of DailyLines on this </h2>
                <InfiniteScroll
          dataLength={this.state.articles.length}
          next={this.fetchMoreData}
          hasMore={this.state.articles.length!==this.state.totalResults}
          loader={<Spinner/>}
        >
            

                <div div className="row">
                    {this.state.articles.map((element) => {
                        return <div className="col-md-4">
                            <NewsItem title={element.title ? element.title.slice(0, 45) : ''} description={element.description ? element.description.slice(0, 66) : ""} imageUrl={element.urlToImage} newsUrl={element.url} Author={element.author} Date={element.publishedAt}  source={element.source.name} />
                        </div>
                    })}
                </div>
            
                </InfiniteScroll>

                </div>

               
             


                </>      


        )
    }
}

export default News
